<template>
  <div>
    <TheHeader :ins="5"></TheHeader>
    <div class="main-center">
      <div class="my-main-box">
        <div v-if="userType == 'fill'">
          <FillCard :dataForm="dataForm"></FillCard>
        </div>
        <div v-if="userType == 'for'">
          <SearchCard :dataForm="dataForm"></SearchCard>
        </div>
        <div v-if="userType == 'all'">
          <AllCard :dataForm="dataForm"></AllCard>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TheHeader from "./../../components/TheHeader";
import FillCard from "./fillCard.vue";
import SearchCard from "./searchCard";
import AllCard from "./allCard"
import { myInfo } from "./../../api/index";
export default {
  data() {
    return {
      userType: "fill",
      dataForm: [],
      roleCode: 0,
    };
  },
  components: {
    TheHeader,
    FillCard,
    SearchCard,
    AllCard
  },
  created() {
    const users = JSON.parse(window.localStorage.getItem("userInfo")).user_role;
    if (users == "fill") {
      this.userType = "fill";
    } else if (users == "all"||users == "超级管理") {
      this.userType = "all";
    } else if (users == "for" || users == "填写") {
      this.userType = "for";
    }
    // this.userType = "all";
    this.getDataInfo();
  },
  computed: {},
  mounted() {},
  methods: {
    getDataInfo() {
      myInfo(
        // "all"
        this.userType
      )
        .then((res) => {
          console.log(res);
          if (res.code == 200) {
            this.dataForm = res;
          } else {
            this.notify(res.msg ? res.msg : "获取失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
<style lang="less" scoped>
.my-main-box {
  margin: 30px 0;
}
</style>
