<script>
import { installSave } from "./../../api/index";
export default {
  data() {
    return {
      dataSubForm: {
        custom_id: "",
        tasktype: "",
        finishtime: "",
        money: "",
      },
      pageSize: 10, // 一页显示多少条数据
      currentPage: 1, // 当前页
    };
  },
  computed: {
    // 计算当前表格中的数据
    data() {
      return this.dataForm.yiwancheng.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      );
    },
  },
  props: {
    dataForm: {
      type: Object,
    },
  },
  methods: {
    getBirthDate(date) {
      this.dataSubForm.finishtime = date;
    },
    submit() {
      if (
        this.dataSubForm.custom_id != "" &&
        this.dataSubForm.tasktype != "" &&
        this.dataSubForm.finishtime != "" &&
        this.dataSubForm.money != ""
      ) {
        installSave({
          custom_id: this.dataSubForm.custom_id,
          tasktype: this.dataSubForm.tasktype,
          finishtime: this.dataSubForm.finishtime,
          money: this.dataSubForm.money,
        })
          .then((res) => {
            // console.log(res);
            this.$message({
              message: "提交成功",
              type: "success",
            });
            const that = this;
            setTimeout(function () {
              that.$router.go(0);
            }, 500);
            if (res.code == 200) {
              console.log();
            } else {
              this.notify(res.msg ? res.msg : "登录失败", "error");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$message({
          message: "填写不完整，不允许提交！",
          type: "error",
        });
      }
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
  },
};
</script>
